*,*::before,*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  height: 100vh;
  background-color: white;
  font-family: "Open Sans", sans-serif;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 135px;
  grid-template-areas: "header";
}

.game__container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 70px 280px 40px;
  grid-template-areas: "counter counter counter" "form form form" "feedback feedback feedback";
  justify-items: center;
  align-items: center;
  font-family: unset;
  font-size: rem(28);
  font-weight: 400;
}

.game__header {
  grid-area: header;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 50px 10px 100px;
  grid-template-areas: "title title title" "square square square" "subtitle subtitle subtitle";
  justify-items: center;
  align-items: center;
  padding-top: 100px;
}

.game__title {
  grid-area: title;
  width: 315px;
  text-align: center;
  font-family: "Monoton", cursive;
  font-size: 50px;
  font-weight: unset;
}

.decoration {
  z-index: -1;
  align-self: self-end;
  grid-area: square;
  background-color: rgba(128, 128, 128, 0.562);
  height: 70px;
  width: 400px;
}

.game__subtitle {
  grid-area: subtitle;
  font-family: "Cute Font", cursive;
  font-size: rem(56);
  font-weight: 700;
}

.game__counter {
  grid-area: counter;
  display: inline-block;
  justify-self: flex-end;
  border: 5px solid rgba(128, 128, 128, 0.562);
  height: 100px;
  width: 100px;
  text-align: center;
  padding-top: 12px;
  margin-right: 20px;
}

.game__form {
  grid-area: form;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  grid-template-areas: "title-input" "input" "btn" "reset";
  justify-items: center;
  align-items: center;
}

.game__input-title {
  grid-area: title-input;
}

.game__input {
  grid-area: input;
  width: 50px;
  height: 50px;
  display: block;
  border: 5px solid grey;
  border-radius: 15px;
  font-size: 20px;
  text-align: center;
}

.game__input:focus {
  border: 1px solid rgba(154, 206, 50, 0.644);
}

.game__button {
  grid-area: btn;
  width: 50px;
  height: 30px;
  display: block;
  border: 1px solid grey;
  border-radius: 5px;
  font-size: 10px;
  text-align: center;
}

.game__button:focus {
  border: 1px solid rgba(128, 128, 128, 0.562);
}

.game__reset {
  grid-area: reset;
}

.game__feedback {
  grid-area: feedback;
  font-family: "Homemade Apple", cursive;
}
